
$color-primary: #004a7c;
$color-secondary: #005691;
$color-tertiary: #e8f1f5;
$color-white: #fafafa;

.resume {
    background-color: $color-tertiary;


    padding: 10rem 5rem 10rem 5rem;

    &-container {
        display: flex;
        flex: 1;
        overflow: hidden;
    }

    &-header {
        color: $color-primary;
        font-size: 8rem;
    }

    @media only screen and (max-width: 600px) {
        padding: 2rem 0rem 2rem 0rem;
        &-header {
            font-size: 6rem;
        }
    }
}

@keyframes grow {
    0% {
        width: 0rem;
    }

    50% {
        width: 15rem;
    }

    100%{
        width: 0rem;
    }
}

@keyframes show {
    0% {
        opacity: 0;
    }

    100%{
        opacity: 1;
    }
}

.timeline {
    flex: .1;
    padding: 1rem 2rem 0rem 2rem;
    display: flex;
    align-items: center;

    &-section {
        position: relative;
        height: 3rem;
        width: 3rem;
        border-radius: 100%;
        opacity: .8;
        svg {
            width: 100%;
            height: 100%;
        }

        &-empty {
            background-color: $color-primary;
        }
        position: absolute;

        &-animate {
            width: 0px;
            height: .6rem;
            background-color: $color-primary;
            position: absolute;
            border-radius: 100px;
            left: 120%;
            top: 50%;
            animation-name: grow;
            animation-duration: 1s;
            animation-timing-function: ease-in;
            z-index: -100;
        }
    }

    &-container {
        margin-right: 5rem;
        margin-top: 5rem;
    }

    &-line {
        height: 70vh;
        width: 1rem;
        background: linear-gradient(180deg, rgba(0, 86, 145, 0.5) 0%, rgba(0, 74, 124, 0) 100%);
        border-radius: 10px;
        position: relative;
        margin: 0rem 2rem 0rem 1rem;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
        justify-content: center;
        display: flex;
    }

    &-clock {
        position: absolute;
        left: -2rem;
        display: flex;
        z-index: 1000;

        svg {
            width: 5rem;
            height: 5rem;
            min-width: 5rem;
        }
    }

    &-dates {

        z-index: 1000;

        &__end {
            font-size: 3rem;
            margin: 1rem 1rem 1rem 1rem; 
            color: $color-primary;
        }

        &__start {
            font-size: 2rem;
            color: $color-primary;
            opacity: .8;
            margin: 1rem 1rem 1rem 2rem; 
        }
    }
}

.content {
    position: relative;
    margin: 2rem;
    background-color: $color-white;
    border-radius: 10px;
    padding: 2rem;
    max-width: 600px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    min-height: 70rem;
    border: 1px solid #eeeeee;

    display: flex;
    flex-direction: column;
    justify-content: center;


    &-show {
        animation-name: show;
        animation-duration: 1s;
        animation-timing-function: ease-in;
    }

    &-resume-icon {
        height: auto;
        width: 50%;
        margin: 2rem;
    }

    &-header {
        text-align: center;
        font-size: 4rem;
        color: $color-primary;
       h1 {
           margin: 1rem;
       }
    }

    &-sub-header {
        text-align: center;
        font-size: 2rem;
        color: $color-secondary;

        border-bottom: 1px solid rgba(0, 74, 124, 0.5);
        h2 {
            margin: 1rem;
        }
    }

    &-description {
        flex: .4;
        ul {
            li {
                font-size: 3rem;
                color: $color-primary;
            }
        }
    }

    @media only screen and (max-width: 600px) {
        &-header {
            font-size: 3rem;
        }

        &-sub-header {
            font-size: 1.5rem;
        }

        &-description {
            ul {
                padding-inline-start: 2rem;
                li {
                    font-size: 2rem;
                }
            }
        }
    }
}

.icon {
    position: absolute;
    right: 20px;
    top: 20px;
    background-color: rgba(0, 74, 124, 0.2);
    border-radius: 50%;

    svg {
        height: 10rem;
        width: 10rem;
        fill: $color-primary;
    }

    @media only screen and (max-width: 600px) {
        right: 10px;
        top: 10px;

        svg {
            height: 5rem;
            width: 5rem;
        }
    }
}