$color-primary: #004a7c;
$color-secondary: #005691;
$color-tertiary: #e8f1f5;
$color-white: #fafafa;

html {
    font-size: 10px;

    @media only screen and (max-width: 800px) {
        font-size: 8px;
    }

    @media only screen and (max-width: 600px) {
        font-size: 6px;
    }
}

body {
background-color: $color-primary;
margin: 0px;
}

p, h1, h2, label, input, button, textarea, li {
    font-family: 'Caveat', 'sans-serif';
    font-weight: 100;
}

p {
    max-width: 100vw;
}

b {
   background-color: $color-secondary;
   padding: 1rem;
   font-weight: bold;
   border-radius: 1rem 1rem 1rem 1rem;
}

.row {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    overflow: hidden;
}

.col {
    flex: 1;
    &-1 {
        flex: .1;
    }

    &-2 {
        flex: .2;
    }

    &-4 {
        flex: .4;
    }

    &-5 {
        flex: .5;
        

    @media only screen and (max-width: 800px) {
        flex: 1;
        display: flex;
        justify-content: center;
        width: 100vw;
        }

    }

    &-7 {
        flex: .7;
    }

    &-9 {
        flex: .9;
        @media only screen and (max-width: 800px) {
            flex: 1;
    }
    }
}


.justify-content {
    &-space-evenly {
        justify-content: space-evenly;
    }
    &-center {
        justify-content: center;
    }

    &-end {
        justify-content: flex-end;
    }
}

.align-items {
    &-center {
        align-items: center;
    }
}

.direction-column {
    flex-direction: column;
}

.align-self{
    &-center {
        align-self: center;
    }
}

.m-left{
    &-1 {
        margin-left: 1px;
    }
    &-2 {
        margin-left: 2px;
    }
    &-3 {
        margin-left: 3px;
    }

    &-4 {
        margin-left: 4px;
    }
    
}

.p {
    &-1 { padding: 1rem }
    &-2{ padding: 2rem }
}

.flex {
    display: flex;
}

.text-align {
    &-center {
        text-align: center;
    }
}

.underline {
    text-decoration: underline;
}

.position {
    &-relative {
        position: relative;
    }

    &-absolute {
        position: absolute;
    }
}

//keyframes
@keyframes fade-in {
    0% {
        background-color: transparent;
    }

    100% {
        background-color: $color-white;
    }
}

@keyframes fade-out {
    0% {
        background-color: $color-white;
    }

    100% {
        background-color: transparent;
    }
}

@keyframes show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes hide {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes rotateLink {
    0% {
        transform: rotate(-28deg);
    }

    100% {
        transform: rotate(0deg);
    }
}

@keyframes rotateBackLink {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-28deg);
    }
}

@keyframes rotateHabts {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-20deg);
    }
}

@keyframes rotateBackHabts {
    0% {
        transform: rotate(-20deg);
    }

    100% {
        transform: rotate(0deg);
    }
}

//sections

.section-title {
        font-size: 8rem;
        color: $color-white;
}

.background {
    &-white {
        background-color: $color-white;
    animation-name: fade-in;
    animation-duration: .5s;
    }

    &-transparent {
        background-color: transparent;
        animation-name: fade-out;
        animation-duration: .5s;
    }
}


.navbar {
    position: fixed;
    width: 100%;
    z-index: 10000;
    padding: 0rem 4rem 0rem 4rem;

    .logo {
        margin-right: 0px;
    }

    p {
        font-size: 2.5rem;
        color: $color-primary;
        padding: .5rem;
        border-radius: 5px;
    }

    .menu p {
        text-decoration: underline;

        &:hover {
            background-color: $color-secondary;
            color: $color-tertiary;
        }
    }

    @media only screen and (max-width: 1200px) {
        .nav-divider {
            display: none;
        }

        padding: 1rem 1rem 1rem 1rem;
    }

    .chevron {
        position: relative;
        text-align: center;
        height: 1.5rem;
        width: 1.5rem;

        &-flipped {
            -webkit-transform: rotate(-90deg);
            -moz-transform: rotate(-90deg);
            -ms-transform: rotate(-90deg);
            -o-transform: rotate(-90deg);
            transform: rotate(-90deg);
            right: -6px;
            top: 1px;
        }

        &-initial {
            -webkit-transform: rotate(90deg);
            -moz-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
            -o-transform: rotate(90deg);
            transform: rotate(90deg);
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 20%;
            width: 51%;
            background: $color-primary;
            -webkit-transform: skew(0deg, 40deg);
            -moz-transform: skew(0deg, 40deg);
            -ms-transform: skew(0deg, 40deg);
            -o-transform: skew(0deg, 40deg);
            transform: skew(0deg, 40deg);
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            height: 20%;
            width: 50%;
            background: $color-primary;
            -webkit-transform: skew(0deg, -40deg);
            -moz-transform: skew(0deg, -40deg);
            -ms-transform: skew(0deg, -40deg);
            -o-transform: skew(0deg, -40deg);
            transform: skew(0deg, -40deg);
        }
    }

    
}

.vector-header {
    position: absolute;
    top: 0px;
    left: -50px;
    z-index: 100;
}

@keyframes rotateIn {
    0% {
        transform: translate(441.576px, 8439.33px) rotate(0deg) scale(1);
    }

    100% {
        transform: translate(800px, 8300px) rotate(-80deg) scale(.5);
    }
}


@keyframes rotateOut {
    0% {
        transform: translate(800px, 8300px) rotate(-80deg) scale(.5);
    }

    100% {
        transform: translate(441.576px, 8439.33px) rotate(0deg) scale(1);
    }
}

@keyframes cursor {
    0% {
        width: .5rem;
        opacity: 0;
    }

    50% {
        width: .5rem;
        opacity: 1;
    }

    100% {
        width: .5rem;
        opacity: 0;
    }
}

@keyframes showText {
    0% {
        width: 0%
    }
    100% {
        width: 100%;
    }
}

@keyframes highlight {
    from { background-color: lightblue }
    from { background-color: lightblue }
}

.header {
    // background: linear-gradient(180deg, #E8F1F5 0%, rgba(232, 241, 245, 0) 100%);
    background-color: $color-tertiary;
    min-height: 80vh;
    display: flex;
    flex-wrap: wrap;
    padding-top: 5rem;
    position: relative;

    .rotate-in {
        & #Component_12_1 {
            transform-origin: bottom left;
            transform-box: fill-box;
            animation-duration: 1s;
            animation-fill-mode: forwards;
            animation-timing-function: ease-out;
            animation-name: rotateIn;
            animation-delay: .2s;
        }
    }

    .rotate-out {
        & #Component_12_1 {
            transform-origin: bottom left;
            transform-box: fill-box;
            animation-duration: 1s;
            animation-fill-mode: forwards;
            animation-timing-function: ease-out;
            animation-name: rotateOut;
        }
    }

    &-svg {
        height: 100%;
        width: 40vw;

            
        @media only screen and (max-width: 900px) {
            height: 100%;
            min-width: 300px;
        }
    }

    .highlight {
        background-color: lightblue;
    }

    &-text {
        min-width: 300px;
        width: 50vw;
        position: relative;
        display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    
        @media only screen and (max-width: 900px) {
            width: 90vw;
        }
        


        & > h1, h2 {
            width: 1rem;
            max-width: min-content;
            padding: 0rem 0rem 0rem 0rem;
            white-space: nowrap;
            overflow: hidden;
            position: relative;
            animation-name: showText;
            animation-fill-mode: forwards;

            &:before {
                opacity: 0;
                content: '';
                width: .5rem;
                height: 100%;
                background-color: $color-primary;
                position: absolute;
                right: 0px;
                border-radius: 1rem;
                animation-name: cursor;
                animation-fill-mode: forwards;
            }
        }
    }

    &-hi {
       &-show{

       animation-delay: 1s;
        animation-duration: 2s;
        &:before {
            animation-duration: 1s;
            animation-iteration-count: 2;
        }
    } 
    }

    &-name {
        &-show{
        animation-delay: 3s;
        animation-duration: 1s;
        &:before {
            animation-delay: 2s;
            animation-duration: 1s;
            animation-iteration-count: 3;
        }
    }
    }

    &-creator {

        &-show{
            animation-delay: 6s;
            animation-duration: 1s;
            &:before {
                animation-delay: 5s;
                animation-duration: 1s;
                animation-iteration-count: 4;
            }
        }
    }

    h1, h2 {
        color: $color-primary;
    }


    h1 {
         font-size: 12rem;
         margin: 0px;
         position: relative;
    }
    

    .flat-circle{
        position: absolute;
        background: rgba(0, 86, 145, 0.2);
        width: 100%;
        height: 20%;
        top: 55%;
        left: 0%;
        align-self: center;
        border-radius: 100%;
        @media only screen and (max-width: 1200px) {
            width: 60%;
            left: 20%;
        }
    }

    h2 {
        font-size: 6rem;
        margin: 0px;
        text-align: start;
    }
    
    @media only screen and (max-width: 1200px) {
        h1 {
            font-size: 8rem;
        }

        h2 {
            font-size: 5rem;
        }
    }
    

    h2:last-child{
        text-align: end;
    }

    img {
        height: 50rem;
    }

    @media only screen and (max-width: 900px) {
        img {
            height: 40rem;
        }
    }

    @media only screen and (max-width: 700px) {
        img {
            min-height: 30rem;
        }
    }
    

}

.d-curve {
    &:before {
        content: '';
        background-color: $color-tertiary;
        width: 100%;
        height: 10rem;
        border-bottom-right-radius: 100%;
    }
}

.expertise {
    background-color: $color-secondary;
    padding-bottom: 2rem;
    
    &-item {
        background-color: $color-primary;
        color: $color-white;
        display: flex;
        flex-direction: column;
        border-radius: 10px;
        width: 50%;
        align-items: center;
        padding: 3rem;
        margin-bottom: 5rem;

        @media only screen and (max-width: 600px) {
            min-width: 250px;
         }

        p {
            font-size: 3rem;
        }

        & svg {
            fill: $color-white;
            height: 10rem;
            width: auto;
        }

        .custom-svg {
            height: 7rem;
            width: auto;
        }
    }
}

.projects {

    h2 {
        font-size: 4rem;
        color: $color-white;
    }

    ul {
        list-style: none;
        margin-bottom: 5rem;
        li {
            font-size: 3rem;
            color: $color-white;
            text-align: left;
        }
    }

    .additional-imgs {
        display: flex;
        position: absolute;
        justify-content: space-evenly;
        align-items: center;

        opacity: 0;
        z-index: 100;

        @media screen and (max-width: 1200px) {
            display: none;
        }
    }

    .list {
        position: absolute;
        list-style: none;

        li {
            font-size: 4rem;
            color: $color-white
        }

        &-books {
            top: 10%;
            left: 35%;
            opacity: 0;
        }

        &-link {
            opacity: 0;
        }

        &-habts {
            opacity: 0;
        }
    
    }

    .img-overlay {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 74, 124, 0.8);
        opacity: 0;
    }

    .img {
        &-books {
            &:hover {
                .img-overlay, .list-books {
                    animation: show .5s;
                    animation-fill-mode: forwards;
                }
            }

            &:not(:hover){
                .img-overlay, .list-books {
                    animation: hide .5s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &-habts {
            display: flex;
            justify-content: center;
            align-items: center;
            
            img {
                width: 50rem;
            }

            &:hover {
                .img-overlay, .list-habts {
                    animation: show .5s;
                    animation-fill-mode: forwards;
                }

                .img-main {
                    animation: rotateHabts .5s;
                    animation-fill-mode: forwards;
                }

                    .additional-imgs {
                        animation: show .5s;
                        animation-fill-mode: forwards;
                    }
                
            }

            &:not(:hover){
                .img-overlay, .list-habts {
                    animation: hide .5s;
                    animation-fill-mode: forwards;
                }

                .img-main  {
                    animation: rotateBackHabts .5s;
                    animation-fill-mode: forwards;
                }

                    .additional-imgs {
                        animation: hide .5s;
                        animation-fill-mode: forwards;
                    }
                
            }

            &__additional {
                top: 0px;
                right: -50rem;
                width: 100%;
                height: 100%;

                img {
                    height: auto;
                    width: 20rem;
                    border-radius: 20px;
                }

            }
        }

        &-link {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 10px;
            min-width: 300px;

            @media only screen and (max-width: 750px) {
                    width: 100vw;
            }
            

            img {
                width: 30rem;
                min-width: 200px;
            }

            &:hover {
                .img-overlay, .list-link {
                    animation: show .5s;
                    animation-fill-mode: forwards;
                }

        
                .img-main  {
                    animation: rotateLink .5s;
                    animation-fill-mode: forwards;
                }

                .additional-imgs {
                    animation: show .5s;
                    animation-fill-mode: forwards;
                }
            }

            &:not(:hover){
                .img-overlay, .list-link {
                    animation: hide .5s;
                    animation-fill-mode: forwards;
                }

                .img-main {
                    animation: rotateBackLink .5s;
                    animation-fill-mode: forwards;
                }

                .additional-imgs {
                    animation: hide .5s;
                    animation-fill-mode: forwards;
                }
            }

            &__additional {
                top: 0px;
                left: -60rem;
                width: 100%;
                height: 100%;

                img {
                    height: auto;
                    width: 25rem;
                    border-radius: 20px;
                }

            }
        }


    }


    p {
        font-size: 4rem;
        color: $color-white;
        margin: 7rem;
    }

    img {
        min-width: 250px;
        border-radius: 5px;
    }
    
    &-vector {
        fill: $color-secondary;
        height: 20rem;
        position: absolute;
z-index: -1;
width: auto;
        path {
            fill: $color-secondary;
        }

        &-1 {
            top: 0px;
            left: 0px;
            transform: rotate(180deg);
        }

        &-2 {
            top: 0px;
            left: 0px;
        }
        
        &-3 {
            top: 0px;
            right: 20%;
            transform: rotate(00deg);
        }
    }
}

