.contact {
    background-color: $color-secondary;
    padding-bottom: 3rem;
    padding-top: 3rem;

    .form {
        display: flex;
        flex: .5;
        flex-direction: column;
        min-width: 300px;
        width: 100%;
        max-width: 900px;

        h1 {
            margin: 0px;
            color: $color-white;
        }

        p {
            font-size: 2.5rem;
            margin: 2rem;
            color: $color-white;
        }

        label {
            font-size: 2rem;
            color: $color-tertiary;
            margin: 1rem;
        }

        input, textarea {
            border: 2px solid $color-tertiary;
            border-radius: 10px;
            background-color: $color-tertiary;
            width: 95%;
            padding: 1rem;
            color: $color-primary;
            font-size: 2rem;

            &::placeholder{
                color: rgba(0, 74, 124, 0.2);
            }
        }
        textarea {
            height: 100px;
        }

        button {
            background-color: $color-tertiary;
            border-radius: 10px;
            padding: .7rem 1rem .7rem 1rem;
            width: 20rem;
            align-self: center;
            font-size: 2.5rem;
            color: $color-primary;
            border-style: none;
        }
    }
}

