$color-primary: #004a7c;
$color-secondary: #005691;
$color-tertiary: #e8f1f5;
$color-white: #fafafa;

.summary {
    margin-bottom: 5rem;
    
    h1 {
        font-size: 8rem;
        text-align: center;
        color: $color-white;
    }

    h2 {
        font-size: 4rem;
        color: $color-white;
        margin: 1rem 1rem 1rem 1rem;
        & > u {
            text-transform: capitalize;
        }
    }

    p {
        font-size: 3rem;
        color: $color-white;
        margin: 2rem 2rem 2rem 4rem;
    }
}